body {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  margin: 0;
  padding: 0;
  color: #ffffff;
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 1.5;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  background-color: #C4C4C4;
  background-image: url("../img/header-image-desktop.png"), url("../img/header-points-bg-desktop.png"), url("../img/body-bg-desktop.png"), url("../img/body-bg-rect-desktop.png");
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
  background-size: 705px 515px, cover, 100% 881px, 100% 881px;
  background-position: 1030px 255px, 0 0, 0 0, 0 0;
  box-sizing: border-box; }

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  body {
    background-image: url("../img/header-image@2x-desktop.png"), url("../img/body-bg-desktop.png"), url("../img/body-bg-rect-desktop.png"); } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.display-hidden {
  display: none; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.button {
  box-sizing: border-box;
  border-radius: 5px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  cursor: pointer; }

@media (max-width: 1023px) {
  body {
    background-image: url("../img/header-image-tablet.png"), url("../img/header-points-bg-tablet.png"), url("../img/body-bg-tablet.png"), url("../img/body-bg-rect-tablet.png");
    background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
    background-size: 260px 340px, 770px 563px, 100% 692px, 100% 692px;
    background-position: 100% 255px, 100% 135px, 0 0, 0 0; } }

@media (max-width: 767px) {
  body {
    background-image: url("../img/header-image-mobile.png"), url("../img/header-points-bg-mobile.png"), url("../img/body-bg-mobile.png"), url("../img/body-bg-rect-mobile.png");
    background-repeat: no-repeat, no-repeat, no-repeat, no-repeat;
    background-size: 294px 220px, 320px 529px, 100% 647px, 100% 692px;
    background-position: 50% 100px, 100% 35px, 0 0, 0 0; } }

.page-header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding-left: 13.5%;
  padding-right: 13.5%;
  padding-top: 35px;
  box-sizing: border-box; }

@media (max-width: 1023px) {
  .page-header__container {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 30px;
    width: 100%;
    max-width: 1023px;
    margin: 0 auto; } }

@media (max-width: 767px) {
  .page-header__container {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 30px;
    max-width: 767px; } }

.page-header__logo {
  width: 220px;
  height: 52px; }

.page-header__logo-link {
  display: flex;
  justify-content: space-between; }

.page-header__logo-image {
  margin: 0;
  padding: 0;
  width: 52px;
  height: 52px; }

.page-header__logo-text {
  margin: 0;
  padding: 0;
  width: 150px; }

.page-header__logo-name {
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px; }

.page-header__logo-slogan {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 12px;
  color: #788891; }

@media (max-width: 1023px) {
  .page-header__logo-text {
    display: none; } }

.page-header__navigation {
  min-width: 350px;
  min-height: 52px; }

.page-header__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 52px;
  margin: 0;
  padding: 0;
  list-style: none; }

.page-header__item {
  display: inline-block;
  vertical-align: center;
  min-height: 20px;
  font-family: 'PT Mono', 'Arial', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #FFFFFF; }

.page-header__link {
  display: flex;
  color: #ffffff; }

@media (max-width: 1023px) {
  .page-header__link-text {
    display: none; }
  .page-header__navigation {
    min-width: 70px;
    min-height: 52px; } }

.page-header__order {
  box-sizing: border-box; }

.page-header__button {
  padding: 14px 26px 13px 26px;
  border: 1px solid #FFFFFF;
  border-radius: 5px;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  background-color: transparent;
  color: #ffffff; }

@media (max-width: 1023px) {
  .page-header__order {
    display: none; } }

.page-main__wrapper {
  width: 100%;
  height: 100%;
  padding-left: 13.5%;
  padding-right: 13.5%;
  box-sizing: border-box; }

@media (max-width: 1023px) {
  .page-main__wrapper {
    padding-left: 30px;
    padding-right: 30px;
    width: 100%;
    max-width: 1023px;
    margin: 0 auto; } }

.intro__wrapper {
  width: 100%;
  margin: 0 auto;
  padding-top: 225px; }

.intro__title {
  width: 718px;
  height: 94px;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #FFFFFF; }

.intro__message {
  position: relative;
  width: 683px;
  height: 70px;
  margin: 0;
  margin-bottom: 38px;
  padding: 0;
  padding-left: 93px;
  font-family: 'PT Mono', 'Arial', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  color: #FFFFFF; }

.intro__message:before {
  position: absolute;
  content: "";
  width: 66px;
  height: 2px;
  left: 0px;
  top: 17px;
  background: #FFFFFF; }

.intro__button-order {
  margin-left: 95px;
  margin-bottom: 130px;
  padding: 17px 45px 17px 45px;
  border: 1px solid #00B2FF;
  border-radius: 5px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  background-color: #00B2FF;
  color: #ffffff; }

.button--short {
  display: none; }

.intro-scroll {
  margin-left: -40px;
  width: 193px;
  height: 16px; }

.intro-scroll__btn {
  transform: rotate(-90deg);
  font-family: 'PT Mono', 'Arial', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #FFFFFF;
  opacity: 0.5;
  background-color: transparent; }

@media (max-width: 1023px) {
  .intro__title {
    width: 470px;
    height: 141px;
    left: 88px;
    top: 188px;
    font-size: 60px;
    line-height: 70px;
    letter-spacing: 0.07em; }
  .intro__message {
    width: 356px;
    height: 70px;
    font-size: 18px;
    line-height: 156.8%; }
  .intro-scroll {
    display: none; } }

@media (max-width: 767px) {
  .intro__title {
    width: 277px;
    height: 94px;
    font-size: 40px;
    line-height: 47px; }
  .intro__message {
    width: 245px;
    height: 70px;
    font-size: 16px; }
  .intro__button-order {
    margin: 0 auto; }
  .button--short {
    display: block; }
  .button--long {
    display: none; } }
